export function reducer(state, {type,payload}) {
  switch(type) {
    case 'SET_GOODS':
      return {
        ...state,
        goods: payload || [],
        loading: false,
      }
    case "CLOSE_ALERT":
      return {
        ...state,
        alertName: '',
      }
    case 'REMOVE_FROM_BASKET':
       return {
         ...state,
         order: state.order.filter((item) => item.mainId !== payload.id)
       }
    case 'HANDLE_BASKET_SHOW':
        return {
          ...state,
          isBasketShow: !state.isBasketShow,
        }
    case 'ADD_ORDER':
        let itemIndex = state.order.findIndex(elem=> elem.mainId === payload.mainId)
        let newOrder = null;
        if(itemIndex<0){
          const newItem = {
            ...payload,
            quantity: 1,
           };
          newOrder = [...state.order, newItem]
        } else{
            newOrder = state.order.map((elem, i)=> {
              if(i === itemIndex){
                return {
                  ...elem,
                  quantity: elem.quantity+1
                }
              }
              else{
                return elem;
               }
            })
        }
        return {
          ...state,
          order: newOrder,
          alertName: payload.name,
        }
    case 'INCREMENT_QUANTITY':
        return{
          ...state,
          order: state.order.map((item) => {
            if(item.mainId === payload.id){
              const newQ = item.quantity +1;
              return {
                ...item,
                quantity: newQ,
              }
            }
            else{
              return item
            }
          })
        }
    case 'DEGREASE_QUANTITY':
        return {
          ...state,
          order: state.order.map((item) => {
            if(item.mainId === payload.id){
              const newQ = item.quantity -1;
              return {
                ...item,
                quantity: newQ>0 ? newQ : 0,
              }
            }
            else{
              return item
            }
          })
        }
    default:
      return state;
  }
}