import {GoodsItem} from './GoodsItem'
import {useContext} from 'react'
import {ShopContext} from '../context'


export function GoodsList() {
  const {goods=[]} = useContext(ShopContext)

  if(!goods.length){
    return <h3>No Items</h3>
  }

  return (
    <div className='goods'>
      {goods.map((e) =>
        <GoodsItem  key={e.mainId} {...e}/>
      )}
    </div>
  )
}